<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>enableCompass</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> option used to enable or disable the compass.</td>
</tr>
<tr>
<td>enableZoomControl</td>
<td>Boolean|Object</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> option used to enable or disable the zoom control.</td>
</tr>
<tr>
<td>enableDistanceLegend</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> option used to enable or disable the distance legend.</td>
</tr>
<tr>
<td>enableLocationBar</td>
<td>Boolean|Object</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> option used to enable or disable the LocationBar.</td>
</tr>
<tr>
<td>enableCompassOuterRing</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> option used to enable or disable the Compass Outer Ring.</td>
</tr>
<tr>
<td>enablePrintView</td>
<td>Boolean|Object</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> option used to enable or disable the PrintView.</td>
</tr>
<tr>
<td>overrideCamera.overrideCamera</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> Whether the default camera position parameter of the zoom control overrides the camera parameter on the vc-viewer.</td>
</tr>
</tbody>
</table>
<hr>
<h2>Event</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>Triggers when PolylineGraphics is ready. It returns a core class of Cesium, a viewer instance.</td>
</tr>
<tr>
<td>legendChanged</td>
<td>number</td>
<td>Triggered when the scale changes, returns the scale value in meters.</td>
</tr>
<tr>
<td>geolocation</td>
<td>Object</td>
<td>Triggered when positioning is successful and returns the positioning result.</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data () {
        return {
          options: {
            enableCompass: true,
            enableZoomControl: true,
            // enableZoomControl: {
            //   // scaling ratio
            //   zoomAmount: 2,
            //   option used to set a default view when resetting the map view with the reset navigation control.{lng: number, lat: number, height: number} or rectangle{west: number,south: number,east: number,north: number}
            //   defaultResetView: {
            //     lng: 105, lat: 29.999999999999993, height: 19059568.497290563, heading: 360, pitch: -90, roll: 0
            //   }
            // },
            enableDistanceLegend: true,
            enableLocationBar: true,
            // enableLocationBar: {
            //   // Get more accurate elevation
            //   gridFileUrl: 'https://zouyaoji.top/vue-cesium/statics/SampleData/WW15MGH.DAC'
            // },
            enableCompassOuterRing: true,
            enablePrintView: true,
            // enablePrintView: {
            //   // show Credit
            //   showCredit: true,
            //   // print automatically
            //   printAutomatically: false
            // },
            enableMyLocation: true
          }
        }
      },
      methods: {
        ready (cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const {Cesium, viewer} = this.cesiumInstance
        }
      }
    }
  </script>